.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.matchCard{
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    width: 60%;
    padding: 2rem;
    background-color: green;
}

.matchImage {
  width: 5rem;
  height: 5rem;
}

.cardImage{
  width: 9rem;
  height: 9rem;
}

.card {
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    width: 10rem;
    height: 20rem;
    padding: 1rem;
}

.swipePageContainer{
  display: -ms-flexbox;
  display: flex;
-ms-flex-pack: center;
    justify-content: center;

}
.textualContent{
  font-size: 1.3rem;
  text-align: left;
}

.textualContent span{
  font-weight: bold;
}

.cardAndButtons{
  width: 30%;
}


.MatchContainer{
  width: 60%;
  height: 100%;
  background-color: grey;
}

.matchCardsContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}



